import Button from 'antd/es/button'
import { abort } from 'process'
import React, { useEffect, useRef, useState } from 'react'

const ReadMore = props => {
    const [overflow, setOverflow] = useState(false)
    const [open, setOpen] = useState(false)
    const descRef = useRef(null)

    useEffect(() => {
        console.log(descRef.current.offsetHeight)
        if (descRef.current.offsetHeight > props.initialHeight) {
            setOverflow(true)
        }
    }, [descRef.current && descRef.current.offsetHeight, props.content])
    return (
        <div
            style={{
                height: open ? 'initial' : props.initialHeight + 'px',
                overflow: 'hidden',
                position: 'relative',
            }}
        >
            <div
                ref={descRef}
                dangerouslySetInnerHTML={{ __html: props.content }}
            />
            {overflow && (
                <div style={{ position: 'absolute', bottom: 0 }}>
                    <div
                        style={{
                            transition: 'opacity 0.25s',
                            opacity: open ? 0 : 1,
                            backgroundImage:
                                'linear-gradient(to bottom, rgba(255, 255, 255, 0.44), #ffffff )',
                            content: '',
                            height: '50px',
                            width: '100%',

                            bottom: '0',
                            left: '0',
                        }}
                    />

                    <Button onClick={() => setOpen(!open)}>
                        {open ? 'Read Less' : 'Read More'}
                    </Button>
                </div>
            )}
        </div>
    )
}

export default ReadMore
